<template>
  <div class="container-fluid">
    <div class="container mt-4 mb-4">
      <div class="row">
        <div class="col-sm-12 col-md-12 px-5">
          <h2 class="">Giỏ hàng</h2>
          <form action="" @submit.prevent="">
            <table class="table text-center w-100 ">
              <thead>
                <tr>
                  <th>STT</th>
                  <th>Tên hàng</th>
                  <th>Số lượng</th>
                  <th>Đơn giá (đ)</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(food, index) in this.$store.state.listProduct.items"
                  :key="index"
                >
                  <td>{{ index + 1 }}</td>
                  <td>{{ food.foodName }}</td>
                  <td>
                    <input
                      class="text-center quantity"
                      type="number"
                      min="1"
                      step="1"
                      :value="food.quantity"
                    />
                  </td>
                  <td class="price-item">{{ food.price }}</td>
                  <td>
                    <Button
                      @click="deleteProduct(food.foodId)"
                      class="btn btn-delete"
                      ><i class="bi bi-trash"></i
                    ></Button>
                  </td>
                </tr>
              </tbody>
              <tfoot>
                <td colspan="3"><b>Tổng tiền</b></td>
                <td class="total-price">
                  {{ this.$store.state.listProduct.total }}
                </td>
              </tfoot>
            </table>
          </form>
          <div class="message"></div>
          <button @click="confirmOrder" class="btn btn-success btn-accept">Xác nhận đặt hàng</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
import axios from "axios";
export default {
  created: function () {
    this.getListProduct();
    this.getDetailorder();
  },
  computed: {
    ...mapGetters(["detailOrder"]),
  },
  methods: {
    ...mapMutations(["getListProduct", "getDetailorder", "totalProduct"]),
    ...mapActions(["getListProduct", "deleteProduct", "totalProduct"]),
    async deleteProduct(id) {
      var token = localStorage.getItem("curentUser");
      if (token != null) {
        const headers = {
          Authorization: "Bearer" + token,
        };
        const data = {
          foodId: id,
        };
        await axios.delete("/order/delete", { headers, data })
        
        .catch((e) => {
          alert(e);
        });
      }
    },
    async getDetailorder({commit}) {
      var token = localStorage.getItem("curentUser");
      if (token != null) {
        var orderId = null;
        const headers = {
          Authorization: "Bearer" + token,
        };
        this.$store.state.listProduct.items.forEach((value) => {
          orderId = value.orderId;
        });
        await axios
          .get("/order/user/detail/" + orderId, { headers })
          .then((res) => {
            commit("getDetailorder", res.data.data);
          });
      }
    },
    async confirmOrder() {
      var token = localStorage.getItem("curentUser");
      if (token != null) {
        alert('Chức năng đang phát triển !!! Vui lòng đặt hàng trực tiếp bằng số điện thoại')
      }
    },
  },
};
</script>

<style>
.btn-accept {
  display: block;
  margin-left: auto;
}
.total-price {
  padding: 0.5rem;
}
.btn-delete:hover {
  color: #d8ab37;
  background-color: rgba(0, 0, 0, 0.8);
}
.quantity {
  border: none;
}
input:focus {
  outline: none;
}
</style>